h1 {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
body {
  margin: 0;
  font-family: "Muli", sans-serif;
}

* {
  box-sizing: border-box;
}

.wrapper {
  background: #56CCF2;
  padding: 50px 0 400px;
  margin-bottom: 435px;
}

.container {
  max-width: 1024px;
  margin: 0 auto;
  position: relative;
}

.dia {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #fff;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
}
.dia__logo {
  font-size: 32px;
}
.dia__navigation {
  display: flex;
  justify-content: space-between;
  flex-basis: 50%;
  list-style: none;
}
.dia__navigation a {
  color: #fff;
  text-decoration: none;
}

.digital-agency {
  padding-left: 75px;
  color: #fff;
}
.digital-agency__heading {
  font-style: normal;
  font-weight: 900;
  font-size: 64px;
  line-height: 150%;
}
.digital-agency__textual {
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 150%;
}

.button {
  width: 226px;
  height: 56px;
  line-height: 56px;
  background: #fff;
  border-radius: 64px;
  text-align: center;
  color: #253757;
  font-weight: bold;
  border: none;
  font-size: 16px;
}

.context {
  width: 1146px;
  height: 640px;
  position: absolute;
  top: 500px;
  left: 50%;
  transform: translate(-50%);
  background: linear-gradient(0deg, rgba(37, 55, 87, 0.1), rgba(37, 55, 87, 0.1));
  background-image: url(../images/header_photo.svg);
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: cover;
}
.context__navigation {
  display: flex;
  justify-content: space-between;
  position: absolute;
  top: 50%;
  left: 84%;
  transform: rotate(90deg);
  width: 40%;
  padding: 0;
  list-style: none;
}
.context__navigation a {
  text-decoration: none;
  color: #253757;
  font-size: 16px;
}

.context_buttons {
  display: flex;
  position: absolute;
  top: 50%;
  left: -14%;
  transform: translate(50%, -25%);
  border-radius: 50%;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.25) 0%, rgba(255, 255, 255, 0) 100%), #FCC304;
}
.context_buttons .context_button {
  height: 64px;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.25) 0%, rgba(255, 255, 255, 0) 100%), #FCC304;
  border: none;
  outline: none;
  width: 80px;
  background-repeat: no-repeat;
  background-position: 50% 50%;
}
.context_buttons .context_button-left {
  border-radius: 50% 0 0 50%;
  background-image: url(../images/icon-arrow-left.svg);
}
.context_buttons .context_button-right {
  border-radius: 0 50% 50% 0;
  background-image: url(../images/icon-arrow-right.svg);
}

.contect__text {
  position: absolute;
  left: 98px;
  bottom: 86px;
  font-style: normal;
}
.contect__text .initro_text {
  font-weight: 800;
  font-size: 13px;
  line-height: 150%;
  color: #56CCF2;
}
.contect__text .textual_text {
  font-weight: 900;
  font-size: 20px;
  line-height: 150%;
  color: #fff;
}

.welcome {
  max-width: 700px;
  margin: 0 auto;
}
.welcome__header {
  font-weight: 800;
  font-size: 16px;
  line-height: 150%;
  color: #56ccf2;
  display: flex;
  align-items: center;
  letter-spacing: 2px;
  text-transform: uppercase;
}
.welcome__header:before {
  content: url("../images/icon_app.svg");
  width: 21px;
  height: 21px;
  margin-right: 15px;
}
.welcome__text {
  font-weight: 600;
  font-size: 24px;
  margin: 0;
  line-height: 150%;
  color: #253757;
}

.what-we-do {
  max-width: 1146px;
  margin: 136px auto 162px;
}
.what-we-do__header {
  font-style: normal;
  font-weight: 900;
  font-size: 48px;
  line-height: 100%;
  text-align: center;
  color: #253757;
}

.product-group {
  display: grid;
  grid-template-columns: repeat(3, minmax(100px, 1fr));
  justify-items: center;
  margin-top: 136px;
}

.product {
  display: flex;
  flex-direction: column;
}
.product__image {
  background-image: url(../images/productfirst.svg);
  width: 160px;
  height: 160px;
  background-position: 25% 25%;
  background-repeat: no-repeat;
}
.product__short-info {
  font-weight: 900;
  font-size: 20px;
  line-height: 150%;
  color: #253757;
  margin: 64px 0 32px;
}
.product__full-info {
  font-size: 16px;
  line-height: 150%;
  color: #334563;
}

.process-content {
  margin: 0 64px;
  background-color: #fcfcfd;
  padding-bottom: 291px;
  padding-top: 160px;
}

.process,
.features,
.testimonials {
  max-width: 362px;
}
.process__header-short_info,
.features__header-short_info,
.testimonials__header-short_info {
  font-weight: 900;
  font-size: 32px;
  line-height: 140%;
  color: #253757;
}
.process__header,
.features__header,
.testimonials__header {
  font-weight: 800;
  font-size: 16px;
  line-height: 150%;
  color: #56ccf2;
  display: flex;
  align-items: center;
  letter-spacing: 2px;
  text-transform: uppercase;
}
.process__header:before,
.features__header:before,
.testimonials__header:before {
  content: url("../images/icon_app.svg");
  width: 21px;
  height: 21px;
  margin-right: 15px;
}
.process__text,
.features__text,
.testimonials__text {
  font-weight: normal;
  font-size: 16px;
  margin: 0;
  line-height: 150%;
  color: #334563;
}

.card {
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  min-height: 256px;
}

.card__circle {
  position: relative;
}
.card__circle::after {
  content: "";
  display: block;
  width: 48px;
  height: 48px;
  opacity: 0.4;
  border-radius: 50%;
}
.card__circle::before {
  position: absolute;
  display: block;
  top: 13px;
  left: 13px;
  font-style: normal;
  font-weight: 900;
  font-size: 40px;
  line-height: 150%;
  letter-spacing: 2px;
}

.card__first::after {
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.25) 0%, rgba(255, 255, 255, 0) 100%), linear-gradient(0deg, #56CCF2, #56CCF2), #C4C4C4;
  opacity: 0.1;
}
.card__first::before {
  content: "1";
  color: #56CCF2;
}

.card__three::after {
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.25) 0%, rgba(255, 255, 255, 0) 100%), linear-gradient(0deg, #03D542, #03D542), #C4C4C4;
  opacity: 0.1;
}
.card__three::before {
  content: "3";
  color: #03D542;
}

.card__four::after {
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.25) 0%, rgba(255, 255, 255, 0) 100%), #F25656;
  opacity: 0.1;
}
.card__four::before {
  content: "4";
  color: #F25656;
}

.card__two::after {
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.25) 0%, rgba(255, 255, 255, 0) 100%), #FCC304;
  opacity: 0.1;
}
.card__two::before {
  content: "2";
  color: #FCC304;
}

.group-card {
  position: absolute;
  left: 60%;
  top: 0;
  transform: translate(-50%);
  display: grid;
  grid-template-columns: repeat(1, minmax(240px, 1fr));
}

.card:nth-of-type(1) {
  position: relative;
  left: 0;
  top: 128px;
}
.card:nth-of-type(2) {
  position: relative;
  left: 300px;
  top: -256px;
}
.card:nth-of-type(3) {
  position: relative;
  left: 0;
  top: 0;
}
.card:nth-of-type(4) {
  position: relative;
  left: 300px;
  top: -376px;
}

.wrapper-features {
  background-image: url(../images/background-features.svg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  padding: 198px 0 100px;
}

.features__header {
  color: #fff;
}
.features__list-item {
  background-image: url(../images/features_item_1.svg);
  background-repeat: no-repeat;
  background-size: 64px 64px;
  background-position: left center;
  padding-left: 96px;
  margin-bottom: 64px;
}
.features__text {
  color: #fff;
}

.testis {
  display: grid;
  grid-template-columns: repeat(3, minmax(100px, 1fr));
  grid-gap: 80px;
  justify-items: center;
  margin-top: 86px;
  margin-bottom: 86px;
}

.testis__person {
  height: 464px;
  width: 100%;
  text-align: center;
  background: #FEFEFE;
  box-shadow: 16px 16px 32px rgba(0, 0, 0, 0.05);
}

.testis__image {
  position: relative;
}
.testis__image__person {
  position: absolute;
  top: 95px;
  z-index: 1;
  left: 50%;
  transform: translate(-50%);
}
.testis__image__background {
  position: absolute;
  top: 38px;
  left: 50%;
  transform: translate(-50%);
}

.testis__text {
  padding-top: 213px;
}

.testies__person-first {
  width: 64px;
  height: 2px;
  margin: 31px auto;
  z-index: 5;
}

.testies__person-name {
  font-size: 12px;
  line-height: 150%;
  font-weight: 700;
  color: #152032;
  font-weight: 800;
  font-size: 12px;
  line-height: 150%;
  letter-spacing: 2px;
  text-transform: uppercase;
}

.testies__person-position {
  font-weight: 800;
  font-size: 12px;
  line-height: 150%;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: #56ccf2;
}

.testis__text {
  font-size: 14px;
  line-height: 150%;
  font-style: italic;
  color: #253757;
}

